:root {
  --main-font: "Montserrat", sans-serif;
  --footer-font: "Inter", sans-serif;
  --white-bg-color: #fff;
  --footer-bg-color: #000;
  --menu-mobile-bg-color: #e86f54;
  --modal-bg-color: #0000004d;
  --black-text-color: #000;
  --white-text-color: #fff;
  --form-text-color: #5c5c5c;
  --input-text-color: #0006;
  --default-btn-color: #e86f54;
  --hover-btn-color: #e25b41;
  --focus-btn-color: #fa6f54;
  --disablet-btn-color: #e86f5466;
  --box-shadow-focus-btn: 0px 4px 16px #fa6f5433;
  --btn-border-radius: 38px;
}

.subtitle {
  font-family: var(--main-font);
  color: var(--black-text-color);
  text-align: left;
  text-transform: uppercase;
  align-items: center;
  gap: 14px;
  margin-bottom: 32px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.2;
  display: flex;
}

.subtitle:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--black-text-color);
  display: inline-block;
}

@media screen and (min-width: 768px) {
  .subtitle {
    gap: 16px;
    margin-bottom: 60px;
    font-size: 44px;
  }

  .subtitle--about {
    margin-bottom: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .subtitle {
    margin-bottom: 80px;
  }

  .subtitle--about {
    margin-bottom: 26px;
  }
}

.small-title {
