  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
}

.btn {
  min-width: 208px;
  min-height: 44px;
  font-family: var(--main-font);
  color: var(--white-text-color);
  border-radius: var(--btn-border-radius);
  background-color: var(--default-btn-color);
  cursor: pointer;
  border: none;
  margin: 0 auto;
  padding: 10px 35px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
  display: block;
}

.btn:hover {
  background-color: var(--hover-btn-color);
}

.btn:focus {
